.disabledPrimary {
  background-color: var(--gray-a);  
  color: var(--white);
  
}

.disableSecondary {
  background-color: var(--white);  
  color: var(--gray-a);
}

.primary {
  background-color: var(--dark-blue);  
  color: var(--white);
}

.secondary {
  background-color: var(--white);
  color: var(--blue);
}

.large {
  background-color: var(--blue);  
  width: 28px;
}


.fullWidth {
  background-color: var(--blue);  
  width: 100%;
}


body {
  --black: #000;
  --blue: #0075d9;
  --transparent-blue-70: #0075d9B3;
  --medium-blue: #015297;
  --dark-blue: #00416a;
  --gray-a: #A7A9AC;
  --gray-5: #58595b;
  --gray-8: #808285;
  --gray-d: #D1D3D4;
  --gray-e2: #E2E3E4;
  --gray-e: #EDEDEE;
  --gray-f: #F4F4F4;
  --white: #FFFFFF;
}

h2 { margin-top: 200px; }
h2:first-of-type { margin-top: 0; }



